import axios from 'axios';
import { API } from '../config/keys';

const validKey = localStorage.getItem('key');
// list user
export const listUser = () => dispatch => {
  axios.post(`${API}:acl:user`, { x: '1', key: `${validKey}` }).then(res => {
    if (res.data[0].r === '200') {
      dispatch({
        type: 'LIST_USER',
        payload: res.data[0].atom.data
      });
    } else if (res.data[0].r === '401') {
      dispatch({
        type: 'ERROR'
      });
    }
  });
};
// user group
export const listUserGroup = () => dispatch => {
  axios.post(`${API}:acl:group`, { x: '1', key: `${validKey}` }).then(res => {
    if (res.data.r === '200') {
      dispatch({
        type: 'LIST_USER_GROUP',
        payload: res.data.atom.data
      });
    }
  });
};
